
import Dropdown from 'react-bootstrap/Dropdown'

//CSS
import '../assets/css/pages/notifications.css';

//Icons
import IcDelete from '../assets/images/icons/ic-delete.svg';
import IcBellOff from '../assets/images/icons/ic-bell-off.svg';

//Images
import employerImg from "../assets/images/employer-4.png";

function Notifications() {
    return (
        <div className="main-content">
            <section className="notifications">
                <div className="container">
                    <div className="section-title varient-two">
                        <h4>Notification</h4>
                    </div>
                    <div className="notifications-list">
                        <ul>
                            {
                                [...Array(10)].map((item, idx) => {
                                    return (
                                        <li key={idx}>
                                            <div className="notifier-img">
                                                <img src={employerImg} alt="" />
                                            </div>
                                            <div className="notification-text">
                                                <p><strong>3 Companies has shown Interest,</strong> consectetur adipiscing elit. Ipsum in mi est vestibulum scelerisque
                                                    vestibulum, libero. Hac aliquet malesuada nunc, urna integer semper orci. Eget ac, placerat urna amet risus gravida.
                                                    A maecenas quis diam, pharetra amet id natoque. Quis sit sed adipiscing elit. Ipsum in mi est vestibulum scelerisque vestibulum, libero.</p>
                                                <Dropdown className="notification-controls" align="end">
                                                    <Dropdown.Toggle variant="" id="dropdown-split-basic" />
                                                        
                                                    <Dropdown.Menu>
                                                        <Dropdown.Item>
                                                            <img src={IcDelete} alt="" />
                                                            Delete
                                                        </Dropdown.Item>
                                                        <Dropdown.Item>
                                                            <img src={IcBellOff} alt="" />
                                                            Turn Off 
                                                        </Dropdown.Item>
                                                    </Dropdown.Menu>
                                                </Dropdown>
                                                <span className='notification-time'>2d</span>
                                            </div>
                                        </li>
                                    )
                                })
                            }


                        </ul>
                    </div>
                </div>
            </section>
        </div>
    )
}

export default Notifications;